.mov-img{
    height: 500px;
}
.mov-layer{
    background-color: rgba(255,255,255,.7);
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all .5s;
    z-index: 5;
    flex-direction: column;
}
.mov:hover .mov-layer{
    top:0;
}
.app-body{
    background-color: rgb(66, 64, 63);
}
